import {Component, ViewChild} from '@angular/core';
import {AlertController, Content, IonicPage, LoadingController, NavController, NavParams} from 'ionic-angular';
import {Aposta} from "../../app/objetos";
import {DomSanitizer} from "@angular/platform-browser";
import {http, global} from "../../app/global";
import {BilhetePage} from "../bilhete/bilhete";

@IonicPage()
@Component({
    selector: 'page-consultar-bilhete',
    templateUrl: 'consultar-bilhete.html',
})
export class ConsultarBilhetePage {

    @ViewChild(Content) content: Content;

    codigo: string;
    url: any;
    aposta: Aposta;

    constructor(public navCtrl: NavController,
                public navParams: NavParams,
                public sanitizer: DomSanitizer,
                public loadCtrl: LoadingController,
                public alertCtrl: AlertController) {
    }

    carregaPdf() {
        let url = 'https://docs.google.com/gview?embedded=true&url=' + this.aposta.url;
        this.url = this.sanitizer.bypassSecurityTrustResourceUrl(url);
    }

    buscaAposta() {

        let load = this.loadCtrl.create({
            content: 'Buscando bilhete...',
        });

        load.present();

        http
            .post('/apostas/consultar', {
                codigo: this.codigo,
            })
            .then((res: any) => {
                let j = res.data;
                if (j.result == 1) {
                    this.aposta = j.aposta;
                    this.carregaPdf();
                    this.content.resize();
                } else {
                    this.alertCtrl.create({
                        title: 'Error!',
                        message: j.message,
                    }).present();
                }
            })
            .catch(() => {
                this.alertCtrl.create({
                    title: 'Erro de conexão',
                    message: 'Não foi possível conectar com o servidor!',
                }).present();
            })
            .then(() => {
                load.dismiss();
            })
    }

    validar() {
        let load = this.loadCtrl.create({
            content: 'Validando aposta...',
        });

        load.present();

        http
            .post('/apostas/validar', {
                token: this.aposta.token,
            })
            .then((res: any) => {
                let j = res.data;
                if (j.result == 1) {
                    global.user.creditos = j.saldo;
                    this.navCtrl.setRoot(ConsultarBilhetePage);
                    this.navCtrl.push(BilhetePage, {aposta: this.aposta});
                } else {
                    this.alertCtrl.create({
                        title: 'Error!',
                        message: j.message,
                    }).present();
                }
            })
            .catch(() => {
                this.alertCtrl.create({
                    title: 'Erro de conexão',
                    message: 'Não foi possível conectar com o servidor!',
                }).present();
            })
            .then(() => {
                load.dismiss();
            })
    }

}



