import {Component, ViewChild} from '@angular/core';
import {Content, IonicPage, NavController, NavParams} from 'ionic-angular';
import {Aposta} from "../../app/objetos";
import {DomSanitizer} from "@angular/platform-browser";
import {SocialSharing} from "@ionic-native/social-sharing";
import {Printer, PrintOptions} from "@ionic-native/printer";

@IonicPage()
@Component({
    selector: 'page-bilhete',
    templateUrl: 'bilhete.html',
})
export class BilhetePage {

    @ViewChild(Content) content: Content;

    aposta: Aposta;

    baseUrl = 'https://docs.google.com/gview?embedded=true&url=';

    url: any;

    constructor(public navCtrl: NavController, public navParams: NavParams,
                public sanitizer: DomSanitizer,
                public share: SocialSharing,
                public printer: Printer) {
    }

    ionViewDidEnter() {
        this.aposta = this.navParams.get('aposta');
        let url = this.baseUrl + this.aposta.url;
        this.url = this.sanitizer.bypassSecurityTrustResourceUrl(url);
        this.content.resize();
    }

    imprimir() {

        let options: PrintOptions = {
            name: 'Aposta ' + this.aposta.codigo,
            grayscale: true,
        };

        this.printer.print(this.aposta.url + '/html', options);

    }

    compartilhar() {
        this.share.shareViaWhatsApp("Aposta " + this.aposta.codigo, '', this.aposta.url);
    }

}



