import {BrowserModule} from '@angular/platform-browser';
import {ErrorHandler, NgModule} from '@angular/core';
import {IonicApp, IonicErrorHandler, IonicModule} from 'ionic-angular';
import {SplashScreen} from '@ionic-native/splash-screen';
import {StatusBar} from '@ionic-native/status-bar';

import {MyApp} from './app.component';
import {HomePage} from '../pages/home/home';
import {LoginPage} from "../pages/login/login";
import {ApostaPage} from "../pages/aposta/aposta";
import {CotacoesPage} from "../pages/cotacoes/cotacoes";
import {ConfirmarApostaPage} from "../pages/confirmar-aposta/confirmar-aposta";
import {ApostaProvider} from '../providers/aposta/aposta';
import {BilhetePage} from "../pages/bilhete/bilhete";
import {ApostasPage} from "../pages/apostas/apostas";
import {ConsultarBilhetePage} from "../pages/consultar-bilhete/consultar-bilhete";
import {SocialSharing} from "@ionic-native/social-sharing";
import {NgxMaskModule} from "ngx-mask";
import {Printer} from "@ionic-native/printer";
import {DefineDominioPage} from "../pages/define-dominio/define-dominio";
import {AppUpdate} from "@ionic-native/app-update";

@NgModule({
    declarations: [
        MyApp,
        HomePage,
        LoginPage,
        ApostaPage,
        CotacoesPage,
        ConfirmarApostaPage,
        BilhetePage,
        ApostasPage,
        ConsultarBilhetePage,
        DefineDominioPage,
    ],
    imports: [
        BrowserModule,
        NgxMaskModule.forRoot(),
        IonicModule.forRoot(MyApp, {
            mode: 'md',
        })
    ],
    bootstrap: [IonicApp],
    entryComponents: [
        MyApp,
        HomePage,
        LoginPage,
        ApostaPage,
        CotacoesPage,
        ConfirmarApostaPage,
        BilhetePage,
        ApostasPage,
        ConsultarBilhetePage,
        DefineDominioPage,
    ],
    providers: [
        StatusBar,
        SplashScreen,
        {provide: ErrorHandler, useClass: IonicErrorHandler},
        ApostaProvider,
        SocialSharing,
        Printer,
        AppUpdate,
    ]
})
export class AppModule {
}



