import {Component} from '@angular/core';
import {AlertController, IonicPage, LoadingController, NavController, NavParams} from 'ionic-angular';
import {http, global} from "../../app/global";
import {ApostaProvider} from "../../providers/aposta/aposta";
import {BilhetePage} from "../bilhete/bilhete";

@IonicPage()
@Component({
    selector: 'page-confirmar-aposta',
    templateUrl: 'confirmar-aposta.html',
})
export class ConfirmarApostaPage {

    constructor(public navCtrl: NavController,
                public navParams: NavParams,
                public alertCtrl: AlertController,
                public loadCtrl: LoadingController,
                public aposta: ApostaProvider) {

    }

    confirmar() {

        let jogos = [];

        this.aposta.getJogos().forEach((j) => {

            jogos.push({
                jogo: j.id,
                cotacao: j.cotacao.cotacao.campo,
                tempo: j.cotacao.tempo,
            });

        });

        let load = this.loadCtrl.create({
            content: 'Enviando dados da aposta...',
        });

        load.present();

        http
            .post('/apostar/apostar', this.aposta.getValues())
            .then((response) => {
                let j = response.data;
                if (j.result == 1) {

                    this.alertCtrl.create({
                        title: "Parabéns!",
                        message: j.message,
                    }).present();

                    global.user.creditos = j.saldo;

                    this.navCtrl.pop();
                    this.aposta.limpaAposta();
                    this.navCtrl.push(BilhetePage, {aposta: j.aposta});

                } else {
                    this.alertCtrl.create({
                        title: "Error!",
                        message: j.message,
                    }).present();
                }
            })
            .catch((e) => {
                console.log('Error Apostar', e);
                this.alertCtrl.create({
                    title: "Error",
                    message: "Não foi possível concluír a aposta",
                }).present();
            })
            .then(() => {
                load.dismiss();
            });

    }


}



