import {Component, ViewChild} from '@angular/core';
import {LoadingController, Nav, Platform} from 'ionic-angular';
import {StatusBar} from '@ionic-native/status-bar';
import {LoginPage} from "../pages/login/login";
import {global, http} from "./global";
import {ApostaPage} from "../pages/aposta/aposta";
import {ApostasPage} from "../pages/apostas/apostas";
import {ConsultarBilhetePage} from "../pages/consultar-bilhete/consultar-bilhete";
import {SplashScreen} from "@ionic-native/splash-screen";
import {DefineDominioPage} from "../pages/define-dominio/define-dominio";
import {AppUpdate} from "@ionic-native/app-update";

@Component({
    templateUrl: 'app.html'
})
export class MyApp {

    rootPage: any = DefineDominioPage;

    @ViewChild(Nav) nav;

    glob;

    pages = [
        {title: 'Nova aposta', page: ApostaPage},
        {title: 'Minhas apostas', page: ApostasPage},
        {title: 'Consultar bilhete', page: ConsultarBilhetePage},
    ];

    constructor(public platform: Platform,
                public statusBar: StatusBar,
                public splash: SplashScreen,
                public loadCtrl: LoadingController,
                public update: AppUpdate) {
        platform
            .ready()
            .then(() => {
                this.statusBar.styleDefault();
                this.glob = global;
                this.splash.hide();
            });
    }

    goPage(v: any) {
        this.nav.setRoot(v.page, v.parans || null);
    }

    sair() {

        let load = this.loadCtrl.create({
            content: 'Saindo...',
        });

        load.present();

        http
            .get('user/logout')
            .then((res: any) => {
                window.localStorage.removeItem('appToken');
                global.appToken = null;
                this.nav.setRoot(LoginPage);
            })
            .catch(() => {
                this.platform.exitApp();
            })
            .then(() => {
                load.dismiss();
            })
    }
}




