import {Component} from '@angular/core';
import {IonicPage, NavController, NavParams} from 'ionic-angular';
import {global} from "../../app/global";
import {Cotacao, CotacaoGrupo, Jogo} from "../../app/objetos";

@IonicPage()
@Component({
    selector: 'page-cotacoes',
    templateUrl: 'cotacoes.html',
})
export class CotacoesPage {

    glob = global;
    jogo: Jogo;
    tempo: string = '90';

    constructor(public navCtrl: NavController, public navParams: NavParams) {
        this.jogo = this.navParams.get('jogo');
    }

    apostar(tempo: string, cotacao: Cotacao) {

        this.jogo.apostaEm = tempo + cotacao.campo;

        if (tempo == '90' && cotacao.principal) {
            this.jogo.outras = false;
        } else {
            this.jogo.outras = true;
        }

        this.jogo.cotacao = {
            id: cotacao.id,
            tempo: tempo,
            valor: this.jogo.cotacoes[tempo][cotacao.campo] || 1,
            cotacao: cotacao,
        };

        this.navCtrl.pop();
    }

    getCotacoes(grupo: CotacaoGrupo) {
        let cotacoes = [];
        this.glob.cotacoes.forEach((c) => {
            if (c.grupo == grupo.id && this.jogo.cotacoes[this.tempo][c.campo] > 1)
                cotacoes.push(c);
        });
        return cotacoes;
    }

}



