import {Injectable} from '@angular/core';
import {global} from "../../app/global";
import {Jogo} from "../../app/objetos";

@Injectable()
export class ApostaProvider {

    cliente: string;
    valor: string;

    constructor() {
    }

    /**
     * Lista de jogos selecionados
     * @returns {Jogo[]}
     */
    getJogos(): Jogo[] {
        let jogos = [];
        global.campeonatos.forEach((c) => {
            c.jogos.forEach((j) => {
                if (j.cotacao)
                    jogos.push(j);
            });
        });
        return jogos;
    }

    /**
     * Valor apostado
     * @returns {number}
     */
    getValor(): number {
        let valor = parseFloat(this.valor);
        if (valor > 0) {
            return valor;
        } else {
            return 0;
        }
    }

    /**
     * Valor da cotação
     * @returns {number}
     */
    getCotacao(): number {
        let jogos = this.getJogos();

        if (jogos.length) {

            let cotacao = 1;

            jogos.forEach((j) => {
                cotacao *= j.cotacao.valor;
            });

            return Math.min(cotacao, global.cotacaoMax);

        } else {
            return 0;
        }
    }

    /**
     * Prêmio da aposta
     * @returns {number}
     */
    getPremio(): number {
        let cotacao = this.getCotacao();
        let valor = this.getValor();
        let premio = cotacao * valor;
        return Math.min(premio, global.retornoMaximo);
    }

    getValues(): {} {

        let values = {
            cliente: this.cliente,
            valor: this.valor,
            jogos: [],
        };

        this.getJogos().forEach((j) => {
            values.jogos.push({
                jogo: j.id,
                cotacao: j.cotacao.cotacao.campo,
                tempo: j.cotacao.tempo,
            });
        });

        return values;

    }

    limpaAposta(): void {
        this.cliente = '';
        this.valor = '0';
        this.limpaJogos();
    }

    limpaJogos(jogo?: Jogo): void {
        if (jogo) {
            jogo.cotacao = null;
        } else {
            this.getJogos().forEach((j) => {
                j.cotacao = null;
            });
        }
    }

    validarAposta(): any {
        if (this.getJogos().length < global.minJogos) {
            return {
                title: "Selecione mais jogos",
                message: 'A aposta deve conter no mínimo ' + global.minJogos + ' jogos!',
            };
        } else if (this.getCotacao() < global.cotacaoMin) {
            return {
                title: "Cotação inválida",
                message: 'A aposta deve atingir uma cotação mínima de: ' + global.cotacaoMin.toFixed(2),
            };
        } else if (this.getValor() < global.apostaMin) {
            return {
                title: "Valor inválido",
                message: 'Aposta mínima de R$ ' + global.apostaMin.toFixed(2).replace('.', ','),
            };
        } else if (this.getValor() > global.apostaMax) {
            return {
                title: "Valor inválido",
                message: 'Aposta máxima de R$ ' + global.apostaMax.toFixed(2).replace('.', ','),
            };
        }
        return true;
    }

}



