import axios from 'axios';
import {Campeonato, Cotacao, CotacaoGrupo, Pais, User} from "./objetos";

export var global: {
    title: string,
    uri: string,
    appId: string,
    appToken: string,
    cotacoes: Cotacao[],
    grupos: CotacaoGrupo[],
    campeonatos: Campeonato[],
    paises: Pais[],
    user: User,
    hoje: string,
    amanha: string,
    apostaMin: number,
    apostaMax: number,
    cotacaoMin: number,
    cotacaoMax: number,
    minJogos: number,
    retornoMaximo: number,
    logotipo: string,
    background: string,
} = {
    hoje: null,
    amanha: null,
    title: 'Mega Esportes',
    uri: 'https://www.megaesportes.net.br/api',
    appId: window.localStorage.getItem('randid'),
    appToken: window.localStorage.getItem('appToken'),
    cotacoes: [],
    logotipo: '',
    background: '',
    grupos: [],
    campeonatos: [],
    paises: [],
    user: null,
    apostaMin: 1,
    apostaMax: 1,
    cotacaoMin: 1,
    cotacaoMax: 1,
    minJogos: 1,
    retornoMaximo: 1,
};

export var http = axios.create({
    headers: {
        appToken: global.appToken,
    },
});




