import {Component} from '@angular/core';
import {AlertController, IonicPage, LoadingController, NavController, NavParams} from 'ionic-angular';
import {http} from "../../app/global";
import {Aposta} from "../../app/objetos";
import {BilhetePage} from "../bilhete/bilhete";

@IonicPage()
@Component({
    selector: 'page-apostas',
    templateUrl: 'apostas.html',
})
export class ApostasPage {

    busca: {};

    constructor(public navCtrl: NavController,
                public navParams: NavParams,
                public loadCtrl: LoadingController,
                public alertCtrl: AlertController) {
    }

    ionViewDidLoad() {
        this.goPage('/apostas/list');
    }

    goBilhete(aposta: Aposta) {
        this.navCtrl.push(BilhetePage, {
            aposta: aposta,
        });
    }

    goPage(url: string) {

        let load = this.loadCtrl.create({
            content: 'Carregando lista de apostas...',
        });

        load.present();

        http
            .get(url)
            .then((res: any) => {
                let j = res.data;
                if (j.result == 1) {
                    this.busca = j;
                } else {
                    this.alertCtrl.create({})
                }
            })
            .catch(() => {
                this.alertCtrl.create({
                    title: '',
                });
            })
            .then(() => {
                load.dismiss();
            });
    }

}



