import {Component} from '@angular/core';
import {
    AlertController,
    IonicPage,
    LoadingController,
    MenuController,
    NavController,
    NavParams,
    Platform
} from 'ionic-angular';
import {global, http} from "../../app/global";
import {ApostaPage} from "../aposta/aposta";

/**
 * Generated class for the LoginPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
    selector: 'page-login',
    templateUrl: 'login.html',
})
export class LoginPage {

    username: string = '';
    password: string = '';
    logotipo: string;
    background: string;

    constructor(public navCtrl: NavController,
                public navParams: NavParams,
                public alertCtrl: AlertController,
                public loadCtrl: LoadingController,
                public platform: Platform,
                public menuCtrl: MenuController) {

    }

    ionViewDidLoad(): void {
        this.logotipo = global.logotipo;
        this.background = global.background;
        this.checkUsuario();
    }

    ionViewDidEnter() {
        this.menuCtrl.enable(false);
    }

    checkUsuario(): void {
        http
            .get('/user/dados')
            .then((response) => {
                let j = response.data;
                if (j.result == 1) {
                    global.user = j.user;
                    this.menuCtrl.enable(true);
                    this.navCtrl.setRoot(ApostaPage);
                }
            })
            .catch(() => {
                // Não faça nada
            });
    }

    entrar() {

        let load = this.loadCtrl.create({
            content: 'Autenticando...',
        });

        load.present();

        http
            .post('/user/login', {
                username: this.username,
                password: this.password,
            })
            .then((response) => {
                let j = response.data;
                if (j.result == 1) {
                    http.defaults.headers.appToken = j.appToken;
                    window.localStorage.setItem('appToken', j.appToken);
                    global.user = j.user;
                    this.menuCtrl.enable(true);
                    this.navCtrl.setRoot(ApostaPage);
                } else {
                    this.alertCtrl.create({
                        title: 'Error!',
                        message: j.message,
                    }).present();
                }
            })
            .catch((e) => {
                console.log(e);
            })
            .then(() => {
                load.dismiss();
            });
    }

    sair() {
        this.platform.exitApp();
    }

}



