import {Component} from '@angular/core';
import {AlertController, IonicPage, LoadingController, NavController, NavParams, Platform} from 'ionic-angular';
import axios from "axios";
import {global, http} from "../../app/global";
import {LoginPage} from "../login/login";

@IonicPage()
@Component({
    selector: 'page-define-dominio',
    templateUrl: 'define-dominio.html',
})
export class DefineDominioPage {

    dominio: string;

    constructor(public navCtrl: NavController,
                public navParams: NavParams,
                public alertCtrl: AlertController,
                public loadCtrl: LoadingController,
                public platform: Platform) {

    }

    ionViewDidEnter() {
        let url = window.localStorage.getItem('uri');
        if (url) {
            http.defaults.baseURL = url;
            this.getInformacoes();
        }
    }

    getInformacoes() {

        let load = this.loadCtrl.create({
            content: 'Carregando informações da banca...',
        });

        load.present();

        http
            .get('/')
            .then((res: any) => {
                let j = res.data;
                if (typeof j == 'object' && j.result == 1) {
                    global.title = j.title;
                    global.apostaMin = j.apostaMin;
                    global.apostaMax = j.apostaMax;
                    global.cotacaoMin = j.cotacaoMin;
                    global.cotacaoMax = j.cotacaoMax;
                    global.minJogos = j.minJogos;
                    global.retornoMaximo = j.retornoMaximo;
                    global.logotipo = j.logotipo;
                    global.background = j.background;
                    this.navCtrl.setRoot(LoginPage);
                } else {
                    window.localStorage.removeItem('uri');
                    this.alertCtrl.create({
                        message: 'Não foi possível validar o domínio, informe um novo domínio.',
                    }).present();
                }
            })
            .catch(() => {
                this.platform.exitApp();
            })
            .then(() => {
                load.dismiss();
            });
    }

    acessar() {

        let load = this.loadCtrl.create({
            content: 'Validando domínio...',
        });

        load.present();

        let dominio = this.dominio
            .replace(/https?:\/\//, '')
            .replace('www.', '')
            .toLowerCase();

        axios
            .get('https://' + dominio + '/api/validar')
            .then((res: any) => {

                let j = res.data;

                if (typeof j == 'object' && j.result == 1) {
                    window.localStorage.setItem('uri', j.url);
                    http.defaults.baseURL = j.url;
                    this.getInformacoes();
                } else {
                    this.alertCtrl.create({
                        message: 'Domínio inválido',
                    }).present();
                }
            })
            .catch(() => {
                this.alertCtrl.create({
                    title: 'Falha',
                    message: 'Não foi possível validar o domínio',
                }).present();
            })
            .then(() => {
                load.dismiss();
            });
    }

}



