import {Component, ViewChild} from '@angular/core';
import {AlertController, Content, IonicPage, LoadingController, NavController, NavParams} from 'ionic-angular';
import {global, http} from "../../app/global";
import {Campeonato, Cotacao, Jogo, Pais} from "../../app/objetos";
import {CotacoesPage} from "../cotacoes/cotacoes";
import {ConfirmarApostaPage} from "../confirmar-aposta/confirmar-aposta";
import {ApostaProvider} from "../../providers/aposta/aposta";

@IonicPage()
@Component({
    selector: 'page-aposta',
    templateUrl: 'aposta.html',
})
export class ApostaPage {

    @ViewChild(Content) content: Content;

    glob = global;

    filtros: {
        time: string,
        data: string,
        campeonato: number,
        pais: number,
    };

    constructor(public navCtrl: NavController,
                public navParams: NavParams,
                public loadCtrl: LoadingController,
                public alertCtrl: AlertController,
                public aposta: ApostaProvider) {

        window.onresize = () => {
            this.content.resize();
        };

    }

    ionViewDidLoad(): void {
        this.load();
    }

    concluirAposta(): void {

        var validar = this.aposta.validarAposta();

        if (validar !== true) {
            this.alertCtrl.create({
                title: validar.title,
                message: validar.message,
            }).present();
        } else {
            this.navCtrl.push(ConfirmarApostaPage);
        }


    }

    getCotacoesPrincipais(): Cotacao[] {
        let cotacoes = [];
        this.glob.cotacoes.forEach((c) => {
            if (c.principal)
                cotacoes.push(c);
        });
        return cotacoes;
    }

    filtrar(jogo?: Jogo, campeonato?: Campeonato, pais?: Pais): boolean {

        if (campeonato) {
            if (!this.filtros.campeonato || campeonato.id == this.filtros.campeonato) {
                if (this.getJogos(campeonato).length) {
                    return true;
                }
            }
        }

        if (jogo) {
            if (!this.filtros.data || jogo.data == this.filtros.data) {
                if (!this.filtros.time) {
                    return true;
                } else {
                    let exp = new RegExp(this.rmAcentos(this.filtros.time), 'i');
                    if (exp.exec(this.rmAcentos(jogo.casa)) || exp.exec(this.rmAcentos(jogo.fora))) {
                        return true;
                    }
                }
            }
        }

        return false;
    }

    rmAcentos(str: string): string {
        let result = str.replace(/[ÀÁÂÃÄÅàáâãäå]/, "a");
        result = result.replace(/[ÈÉÊËèéêë]/, "e");
        result = result.replace(/[Çç]/, "c");
        return result;
    }

    limpaFiltros(): void {
        this.filtros = {
            time: '',
            data: null,
            campeonato: null,
            pais: null,
        };
    }

    getJogos(campeonato: Campeonato): Jogo[] {
        let jogos = [];
        campeonato.jogos.forEach((j) => {
            if (this.filtrar(j)) {
                jogos.push(j);
            }
        });
        return jogos;
    }

    getPaises(): Pais[] {
        let pais = [];
        this.glob.paises.forEach((p) => {
            if (this.getCampeonatos(p, true).length > 0) {
                pais.push(p);
            }
        });
        return pais;
    }

    /**
     * Lista de campeonatos
     * @returns {Campeonato[]}
     */
    getCampeonatos(pais: Pais, filtrar?: boolean): Campeonato[] {
        let campeonatos = [];
        global.campeonatos.forEach((c) => {
            if (pais.id == c.pais) {
                if (filtrar !== false) {
                    if (this.filtrar(null, c)) {
                        campeonatos.push(c);
                    }
                } else {
                    campeonatos.push(c);
                }
            }
        });
        return campeonatos;
    }

    apostar(jogo: Jogo, tempo: string, cotacao: Cotacao): void {

        jogo.apostaEm = tempo + cotacao.campo;

        if (cotacao.principal && tempo == '90') {
            jogo.outras = false;
        } else {
            jogo.outras = true;
        }

        jogo.cotacao = {
            id: cotacao.id,
            tempo: tempo,
            valor: jogo.cotacoes[tempo][cotacao.campo] || 1,
            cotacao: cotacao,
        };
    }

    limpar(jogo?: Jogo): void {
        if (jogo) {
            jogo.cotacao = null;
            jogo.apostaEm = null;
            jogo.outras = false;
        } else {
            this.limpaFiltros();
            this.glob.campeonatos.forEach((c) => {
                c.jogos.forEach((j) => {
                    j.apostaEm = null;
                    j.outras = false;
                    j.cotacao = null;
                });
            });
        }
    }

    countCotacoes(jogo: Jogo): number {

        let count: number = 0;

        for (let key1 in jogo.cotacoes) {
            for (let key2 in jogo.cotacoes[key1]) {
                let valor = jogo.cotacoes[key1][key2];
                if (valor > 1)
                    count++;
            }
        }

        return count;
    }

    maisCotacoes(jogo): void {
        this.navCtrl.push(CotacoesPage, {
            jogo: jogo,
        });
    }

    /**
     * Carrega a lista de jogos
     */
    load(): void {

        this.limpaFiltros();

        let load = this.loadCtrl.create({
            content: "Carregando jogos...",
        })

        load.present();

        http
            .get('/apostar/jogos')
            .then((response) => {
                let j = response.data;
                if (j.result == 1) {
                    this.glob.campeonatos = j.campeonatos;
                    this.glob.paises = j.paises;
                    this.glob.cotacoes = j.cotacoes;
                    this.glob.grupos = j.grupos;
                    this.glob.hoje = j.hoje;
                    this.glob.amanha = j.amanha;
                }
            })
            .catch((e) => {

            })
            .then(() => {
                load.dismiss();
                this.content.resize();
            })
    }

}



